import { red, indigo, blue, pink } from "@mui/material/colors";

import dateObj, { DateObj } from "../apis/DateHelper";

// --------------------     Types     -------------------- //
enum QTR {
  SCU = "Santa Clara University",
  FALL = "fall",
  WINTER = "winter",
  SPRING = "spring",
}
interface Class {
  name: string;
  catalog?: string;
}
interface Quarter {
  quarter: string;
  classes: Class[];
  start?: DateObj;
  end?: DateObj;
}
interface Year {
  year: string;
  quarters: Quarter[];
}
export interface School {
  color?: string;
  degree: string;
  honors?: string;
  location?: string;
  major?: string;
  minor?: string;
  school?: string;
  subtitle?: string;
  years: Year[];
}

// --------------------     helpers     -------------------- //
export const getStart = (quarter: QTR, yy: number): DateObj => {
  switch (quarter) {
    case QTR.FALL:
      return dateObj(`20${yy}-09`);
    case QTR.WINTER:
      return dateObj(`20${yy}-01`);
    case QTR.SPRING:
      return dateObj(`20${yy}-04`);
    default:
      // eslint-disable-next-line no-console
      console.error("Start Error: quarter given does not exist: ", quarter);
      return dateObj();
  }
};

export const getEnd = (quarter: QTR, yy: number): DateObj => {
  switch (quarter) {
    case QTR.FALL:
      return dateObj(`20${yy}-12`);
    case QTR.WINTER:
      return dateObj(`20${yy}-03`);
    case QTR.SPRING:
      return dateObj(`20${yy}-07`);
    default:
      // eslint-disable-next-line no-console
      console.error("End Error: quarter given does not exist: ", quarter);
      return dateObj();
  }
};

